<template>
    <div class='xinwenList'>
        <div class="logo">
            <div class="left">
                <img src="../assets/img/shangcheng/logo.png" alt="">
            </div>
            <div class="right">
                <!-- <div class="sousuo2">
                    <div>
                        <el-input
                            placeholder="请输入内容"
                            v-model="input"
                            clearable>
                        </el-input>
                    </div>
                    <div class="sousuosousuo" @click="sousuo()">
                        搜索
                    </div>
                </div>
                <div class="cen">
                    <div class="list">
                        <div>热门搜索</div>
                        <div class="item" v-for="(item,i) in hot_searches" :key="i" @click="sousuo(item)">{{item}}</div>
                    </div>
                </div> -->
            </div>
        </div>
        <div class="title">新闻列表</div>
        <div class="cen">
            <div class="left">
                <div :class="{checked:title == item.id}" @click="getList(item.id)" v-for="(item,i) in get_article_category" :key="i">{{item.name}}</div>
            </div>
            <div class="right">
                <div class="newsList">
                    <div class="new" v-for="(item,i) in news_list" :key="i" @click="xiangqing(item.id)">
                        <div class="img">
                            <img :src="imgUrl + item.thumb" alt="">
                        </div>
                        <div class="text">
                            <p class="p1">{{item.title}}</p>
                            <p class="p2">{{item.time}}</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            imgUrl:'',
            title:1,
            get_article_category:'',//文章分类
            news_list:'',//新闻列表
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        _this.apis.get(_this.apis.get_article_category,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0 ){
                _this.get_article_category = res.data.data
                _this.title = res.data.data[0].id
                _this.getList(res.data.data[0].id)
            }
        })
    },
    watch:{
    },
    methods:{
        getList(i){
            var _this = this;
            _this.title = i;
            _this.apis.post(_this.apis.news_list,{lang:_this.apis.lang,type_id:i}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.news_list = res.data.data.data
                }
            })
        },
        xiangqing(i){
            var _this = this;
            _this.$router.push({
                path:'xinwen',
                query:{id:i}
            })
        },
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.xinwenList{
    .logo{
        width: 1360px;
        height: 137px;
        margin: 0 auto;
        padding-top: 20px;
        display: flex;
        justify-content: space-between;
        align-items: top;
        .left{
            width: 444px;
            height: 87px;
        }
    }
    .title{
        font-size: 18px;
        font-weight: bold;
        color: #fff;
        height: 44px;
        line-height: 44px;
        margin: 20px 0;
        text-align: center;
        background: #FFAD01;
    }
    .cen{
        width: 1200px;
        min-height: 300px;
        margin: 0 auto;
        margin-bottom: 50px;
        display: flex;
        justify-content: space-between;
        .left{
            width: 210px;
            height: 375px;
            margin-right: 37px;
            text-align: center;
            line-height: 51px;
            background: #FFE7E7;
            div{
                height: 51px;
                font-size: 18px;
                color: #000;
                cursor:pointer;
            }
            .checked{
                background: #262626;
                color: #fff;
                font-size: 20px;
            }
        }
        .right{
            flex: 1;
            height: 650px;
            overflow-y: scroll;
            .new{
                margin-bottom: 20px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                cursor:pointer;
                .img{
                    width: 180px;
                    height: 130px;
                    margin-right: 25px;
                }
                .text{
                    height: 130px;
                    flex: 1;
                    border-bottom: 1px solid #E4E4E4;
                    .p1{
                        height: 90px;
                        line-height: 40px;
                        font-size: 18px;
                        color: #3E3E3E;
                        font-weight: bold;
                        -webkit-line-clamp:2;
                        -webkit-box-orient: vertical;
                        display: -webkit-box;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                    .p2{
                        font-size: 16px;
                        color: #3E3E3E;
                        font-weight: 100;
                    }
                }
            }
        }
    }
}
</style>


